// General.
//
// General settings.

// Enable (true) or disable (false) smooth scrolling.
$enable-smooth-scroll: true !default;

// $root-font-size effects the value of `rem`, which is used for as well font sizes, paddings and margins.
$root-font-size: null !default;

// Font family sans-serif.
$font-family-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;

// Font family monospace.
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;

// Body.
//
// Settings for the `body` element.

$body-font-size: 1rem !default; // Assumes the browser default. Typically `16px`.

$body-font-weight: 400 !default;

$body-line-height: 1.5 !default;

$body-font-color: #212529 !default;

$body-text-align: null !default;

$body-bg-color: #fff !default;

// Headings.
//
// Style `h1`, `h2`, `h3`, `h4`, `h5, `h6` elements.

$headings-margin-bottom: .5rem !default;

// Paragraphs.
//
// Style `p` element.

$paragraph-margin-bottom: 1rem !default;

// Code.
//
// Style `kbd` element.

$kbd-padding-y: .2rem !default;

$kbd-padding-x: .4rem !default;

$kbd-color: #fff !default;

$kbd-bg: #444 !default;

// Z-index.
//
// Set z-index levels, as well as z-index for helper position classes.



$z-index-sticky: 99999 !default;

$z-index-fixed: 999999 !default;

// Spacing.
//
// Controls paddings and margins.
// You can add entries you need in the $spacers map, as well as setting different value calculation for each entry.

$spacer: .25rem !default;

$spacers: (
  0: 0,
  1: $spacer,
  2: $spacer * 2,
  3: $spacer * 3,
  4: $spacer * 4,
  5: $spacer * 5
) !default;

$enable-negative-margins: true !default;

// Breakpoints.
//
// Breakpoints minimum widths for responsive media queries.

$breakpoints: (
  xs: 0, // Extra small (default).
  sm: 576px, // Small.
  md: 768px, // Medium.
  lg: 992px, // Large.
  xl: 1200px // Extra large.
) !default;

// Embedding.
//
// Aspect ratios for fluid media embedding.

$aspect-ratios: (
  (1, 1),
  (4, 3),
  (6, 4),
  (7, 5),
  (8, 5),
  (16, 9),
  (21, 9)
) !default;

$enable-reverse-aspect-ratios: true !default;

// Border radius.
//
// Controls available border-radiuses and border-radius for rounded boxes.

$border-radiuses: (
  0: 0,
  1: 1px,
  2: 2px,
  3: 3px,
  4: 4px,
  5: 5px
) !default;

$border-radius-rounded: 5px !default;

// Transformations.
//
// Controls rotation transformations.

$rotations: (
  0: 0,
  45: 45deg,
  90: 90deg,
  135: 135deg,
  180: 180deg,
  225: 225deg,
  270: 270deg,
  315: 315deg,
  360: 360deg
) !default;

$enable-ccw-rotation: false !default;

// Debug.
//
// Map a different color for each HTML element.

$debug-children-color: #ffd700 !default;

